<template>
  <div>
    <div ref="map" style="width: 800px; height: 600px"></div>
    <div class="searchBox">
      <form>
        <input id="mapSearch" type="text" />
        <input @click="getRoute()" id="mapButton" type="button" value="" />
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      map: "",
      directionsService: "",
      directionsDisplay: "",

      // myLatLng: { lat: -34.397, lng: 150.644 },
    };
  },
  mounted() {
    let script = document.createElement("script");
    script.src =
      "https://maps.googleapis.com/maps/api/js?key=AIzaSyDLklKnRc5oCoIlklBzWDPkfAe9RAa7Ia0&callback=initMap&libraries=places";
    script.async = true;
    document.head.appendChild(script);

    window.initMap = () => {
      // this.directionsService = new window.google.maps.DirectionsService();
      // this.directionsDisplay;
      // var myOptions = {
      //   zoom: 17,
      //   center: new window.google.maps.LatLng(-34.397, 150.644),
      //   mapTypeId: window.google.maps.MapTypeId.ROADMAP,
      //   scaleControl: true,
      // };

      // this.map = new window.google.maps.Map(this.$refs.map, {
      //   myOptions,
      // });
      // new window.google.maps.Marker({ position: this.myLatLng, map: this.map });

      this.directionsService = new window.google.maps.DirectionsService();
      // this.directionsDisplay;

      //
      var myOptions = {
        zoom: 17,
        center: new window.google.maps.LatLng(34.687333, 135.525956),
        mapTypeId: window.google.maps.MapTypeId.ROADMAP,
        scaleControl: true,
      };

      //
      this.map = new window.google.maps.Map(this.$refs.map, myOptions);
    };
  },
  methods: {
    getPlace() {
      var mapSearch = document.getElementById("mapSearch");
      if (mapSearch.value) {
        var service = new window.google.maps.places.PlacesService(this.map);
        var searchValue = mapSearch.value;
        var placeRequest = {
          query: searchValue, //
        };

        //
        service.textSearch(placeRequest, function (results) {
          var places = results[0];
          this.toGeocode(places);
        });
      }
    },
    toGeocode(/* places */) {
      // placesgeocode
      // var latlng = new window.google.maps.LatLng(
      //   places.geometry.location.lat(),
      //   places.geometry.location.lng()
      // );
      //
      this.getRoute(/* latlng */);
    },
    getRoute(/* latlng */) {
      var request = {
        origin: /* latlng */ new window.google.maps.LatLng(
          35.7102849,
          139.77714030000004
        ), //
        // origin: new window.google.maps.LatLng(34.7024, 135.4959),
        destination: /* latlng */ new window.google.maps.LatLng(
          34.687333,
          135.525956
        ),
        travelMode: window.google.maps.DirectionsTravelMode.WALKING, //
      };
      this.directionsService.route(request, function (result) {
        console.log(result);
        alert(result.routes[0].legs[0].distance.text);
        // this.toRender(result);
        this.directionsDisplay = new window.google.maps.DirectionsRenderer();
        this.directionsDisplay.setDirections(result); //set
        this.directionsDisplay.setMap(this.map); //
      });
    },
    toRender(result) {
      console.log(result);
      this.directionsDisplay = new window.google.maps.DirectionsRenderer();
      this.directionsDisplay.setDirections(result); //set
      this.directionsDisplay.setMap(this.map); //
    },
  },
};
</script>
