<template>
  <v-container fill-height>
    <v-row justify="center" align-content="center">
      <v-col cols="6" id="forms">
        <h1></h1>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
          ></v-text-field>

          <v-text-field
            v-model="password"
            :counter="8"
            :rules="passwordRules"
            label=" "
            required
          ></v-text-field>
          <!------------------------------- ------------------------------->
          <input
            type="radio"
            class="mt-5"
            id="client"
            value="client"
            v-model="userType"
          />
          <label for="client"></label>
          <br />
          <!------------------------------- ------------------------------->
          <input
            type="radio"
            id="transporter"
            value="transporter"
            v-model="userType"
          />
          <label for="transporter"></label>
          <!------------------------------- ------------------------------->
          <br /><input
            type="radio"
            class="mb-5"
            id="bothUser"
            value="bothUser"
            v-model="userType"
          />
          <label for="bothUser"></label>
          <br />

          <v-btn
            :disabled="!valid"
            color="success"
            class="mr-4"
            @click="signUp()"
          >
            
          </v-btn>
          <router-link to="/signin"></router-link>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import firebase from "firebase/app";
import "firebase/firestore";
export default {
  data: () => ({
    name: "",
    email: "",
    password: "",
    valid: true,
    userType: "client",
    // ----------------------------- -----------------------------
    emailRules: [
      (v) => !!v || "email",
      (v) => /.+@.+\..+/.test(v) || "email",
    ],
    passwordRules: [
      (v) => !!v || "",
      (v) => (v && v.length >= 6) || "6",
    ],
  }),
  methods: {
    /*  */
    validate() {
      this.$refs.form.validate();
    },
    /*  */
    reset() {
      this.$refs.form.reset();
    },
    /*  */
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    /*  */
    async signUp() {
      this.validate();
      try {
        await firebase
          .auth()
          .createUserWithEmailAndPassword(this.email, this.password);
        this.createUser();
      } catch (error) {
        alert(error.message);
      }
    },
    /*  */
    async createUser() {
      let db = firebase.firestore();
      try {
        await db.collection("users").doc(firebase.auth().currentUser.uid).set({
          uid: firebase.auth().currentUser.uid,
          email: firebase.auth().currentUser.email,
          userType: this.userType,
          createdAt: new Date(),
        });
        alert("");
        this.$router.push("/main-menu/home");
      } catch (error) {
        alert(error.message);
      }
    },
  },
};
</script>

